body {
  font-family: 'PT Sans', sans-serif;
  margin: 0;
  padding: 0;
}

html{overflow-x: hidden}
.tabs {
  cursor: pointer;
  background: #FFF0E9;
  font-family: 'Dancing Script', cursive;
  font-size: 24px;
}
.icons {
  width: 80px;
  height: auto;
}
.items {
  background-color: #f4f4f4;
  padding: 5px;
  border-bottom: 1px #ccc dotted;
}
.padding {
  padding-left: 50px;
}
.btn-add {
  margin-left: auto;
  margin-right: 35px;
  margin-top: 16px;
  border-radius: 50%;
  border: none;
  padding: 10px;
  cursor: pointer;
  width: 50px;
  height: auto;
} 

@media (max-width: 700px) {
  .icons {
    width: 55px;
  }
  .btn-add {
    margin-right: 8px;
  }
}
.input {
    margin-top: 55px;
    margin-left: 20px;
    margin-right: 25px;
    width: 70%;
    border: none;
    border-bottom: 2px solid #8c8c8c;
}
.bg-btn {
    width: 40%;
    background: #E60023;
    border-style: none;
    font-size: 20px;
}
.btn-none {
    border: none;
    width: 16px;
    height: auto;
}
.add {
    padding-right: 5%;
}
.minus {
    padding-left: 6%;
}
.tb-header {
    font-family: 'Dancing Script', cursive;
    font-size: 24px;
}


@media (max-width: 700px) {
    .tb-header {
      font-size: 18px;
    }
    .input {
      width: 85%;
    }
    .bg-btn {
      width: 70%;
      margin-top: 5%;
      margin-left: 12%;
    }
}
.header {
    background: #43414C;
    color: #fff;
    padding: 10px;
    width: 100%;
}
.pointer {
    cursor: pointer;
}
.tittle {
    font-family: 'Dancing Script', cursive;
    font-size: 52px;

}
.secondary {
    font-size: 1.5rem;
}
.list-none {
    color: inherit;
    text-decoration: none;
}
.list-none:hover {
    color: #C09292; 
    text-decoration:none; 
    cursor:pointer;  
}

@media (max-width: 420px) {
    .secondary{
        font-size: 18px;
    }
}

